	import cuCustom from '@/colorui/components/cu-custom.vue'
export default {
	data() {
		return {
			// 控制自定义导航栏是否字体加粗
			isBold: true,
			
			// 商品分类
			esclassify: [],
		}
	},
	onLoad() {
		uni.request({
			url: 'http://47.104.29.236:9999/api/v1/esclassify',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res)
				this.esclassify = res.data.data[1]
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openClass(id) {
			console.log(id)
			uni.navigateTo({
				url: '../bazaarIssue/bazaarIssue?id=' + id,
			});
		}
	},
	components: {
		cuCustom
	}
}
